* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

a {
  text-decoration: none;
  color: inherit;
}

li {
  list-style: none;
}

body {
  color: #333;
  background-color: skyblue;
}

.list {
  max-width: 640px;
  width: calc(100% - 30px);
  margin: 20px auto;
  padding: 20px;
  background-color: white;
}

.list h2 {
  text-align: center;
  font-size: 60px;
  margin-bottom: 40px;
}

.list form {
  display: flex;
  justify-content: space-between;
  position: relative;
  margin-bottom: 30px;
}

.list input {
  width: 100%;
  height: 50px;
  border: 1px solid gray;
  font-size: 14px;
  padding: 0 10px;
}

.list button {
  position: absolute;
  top: 0;
  right: 0;
  width: 15%;
  height: 50px;
  background-color: rgb(245, 245, 245);
  border: 1px solid gray;
  cursor: pointer;
}

.list ul {
  height: 500px;
  overflow-y: scroll;
  margin: 10px 0;
  border: 5px solid black;
  background-color: black;
  position: relative;
}

.list ul li {
  padding: 10px;
  color: white;
  border-bottom: 1px dotted white;
}

.list ul li:last-child {
  border-bottom: none;
}